.dev {
  &-works {
    &__title {
      margin-bottom: 40px;

      @include width-1700 {
        margin-bottom: 20px;
      }
    }
  }
  &-intro {
    margin-top: 80px;

    @include width-768 {
      margin-top: 60px;
    }
    &__content {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @include width-1250 {
        flex-direction: column;
      }
    }
    &__title {
      font-weight: 600;
      font-size: 78px;
      line-height: 100%;
      text-transform: uppercase;
      color: #000000;

      @include width-1250 {
        font-size: 80px;
        margin-bottom: 40px;
      }
      @include width-960 {
        font-size: 64px;
      }
      @include width-640 {
        @include scale(font-size,64,40);
      }
    }
    &__img {
      width: 100%;
      max-width: 496px;
      flex-shrink: 0;
      margin-left: 50px;

      @include width-1250 {
        margin-left: 0;
      }
    }
  }
}

.stages {
  margin-top: 160px;

  &__white {
    margin-top: 0;
  }
  @include width-960 {
    margin-top: 120px;
    &__white {
      margin-top: 0;
    }
  }
  @include width-640 {
    margin-top: 80px;
    &__white {
      margin-top: 0;
    }
  }

  &__title {
    max-width: 1350px;
  }

  &__body {
    margin-top: 40px;
    display: grid;
    grid-template-columns: 505px 1fr;
    grid-template-areas: 'pic text'
                         'pic list';
    column-gap: 40px;

    @include width-1250 {
      grid-template-columns: 1fr;
      grid-template-areas: 'text' 'pic' 'list';
    }
  }

  &__pic {
    background-color: $yellow-dark;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-area: pic;
    & picture {
      position: absolute;
      width: 1500px;
      height: auto;
      & img {
        width: 100%;
        height: 100%;
      }
    }

    @include width-1250 {
      height: 350px;
      margin-top: 40px;
      & picture {
        width: 1200px;
      }
    }
    @include width-960 {
      & picture {
        width: 1000px;
      }
    }
    @include width-640 {
      & picture {
        @include scale(width,1000,700);
      }
    }
  }

  &__text {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    grid-area: text;

    @include width-1450 {
      grid-template-columns: 1fr;
    }
  }

  &-list {
    margin-top: 40px;

    @include width-1250 {
      margin-top: 20px;
    }
    & ol {
      list-style: none;
      margin: 0;
      padding: 0;
      counter-reset: myCounter;
      & li {
        font-weight: 600;
        font-size: 20px;
        line-height: 130%;
        color: #000000;
        position: relative;
        padding: 20px 0;
        padding-left: 94px;
        border-top: 2px solid rgba(0, 0, 0, 0.2);
        cursor: pointer;
        &:last-child {
          border-bottom: 2px solid rgba(0, 0, 0, 0.2);
        }
        &::before {
          counter-increment: myCounter;
          content:counter(myCounter) ".";
          position: absolute;
          left: 0;
          top: 18px;
          font-weight: 600;
          font-size: 20px;
          line-height: 24px;
          color: rgba(0, 0, 0, 0.2);

          @include width-640 {
            @include scale(font-size,20,16);
          }
        }
        & span {
          display: block;
          position: relative;
          width: 100%;
          transition: 0.3s;
          padding-right: 30px;
          &::after {
            content: '';
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto 0;
            width: 20px;
            height: 20px;
            background: url('../img/icons/plus.svg') no-repeat;
            background-size: 20px;
            transition: 0.3s;
          }
          &.active {
            transition: 0.3s;
            &::after {
              transform: rotate(135deg);
              transition: 0.3s;
            }
          }
        }
        & div {
          display: block;
          font-weight: 500;
          font-size: 18px;
          line-height: 130%;
          color: #000000;

          @include width-640 {
            @include scale(font-size,18,16);
          }
          max-width: 700px;
          max-height: 0;
          overflow: hidden;
          transition: max-height .3s ease-out;
          transition: 0.3s;
          &.active {
            margin-top: 10px;
            transition: 0.3s;
          }
        }

        @include width-1250 {
          padding-left: 35px;
        }
        @include width-640 {
          @include scale(font-size,20,16);
        }
      }
    }
  }
}

.stages-items {
  margin-top: 100px;
  grid-area: list;

  @include width-1250 {
    margin-top: 40px;
  }
}
