.marquee {
  display: flex;
  align-items: center;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  background: $yellow-dark;
  border-top: 3px solid #000000;
  border-bottom: 3px solid #000000;
  position: relative;
  padding: 20px 0;
  z-index: 101;
  font-family: "Plex", sans-serif;

  @include width-1700 {
    padding: 13px;
  }

  & h1 {
    font-size: 140px;
    font-weight: 900;
    line-height: 110%;
    white-space: nowrap;
    text-transform: uppercase;

    @include width-1700 {
      font-size: 100px;
    }
    @include width-640 {
      @include scale(font-size,100,54);
    }
  }

  &__wrap {
    margin-top: 50px;

    width: 100%;
    max-width: 100%;
    position: absolute;
    left: 0;
    top: 580px;
    opacity: 0;
    visibility: hidden;
    transition: 2s;
    z-index: 101;
    &.visible {
      opacity: 1;
      visibility: visible;
      transition: 2s;
    }

    @include width-960 {
      top: 660px;
    }
    @include width-768 {
      top: 550px;
    }
    @include width-640 {
      // @include scale(top,550,500);
    }
  }
  &__place {
    opacity: 0;
    visibility: hidden;
    padding: 25px 0;
    margin-top: 50px;
    font-size: 104px;
    font-weight: 900;
    line-height: 110%;
    white-space: nowrap;
    overflow: hidden;

    @include width-1700 {
      font-size: 100px;
      padding: 13px 0;
    }
    @include width-640 {
      @include scale(font-size,100,54);
    }
  }
}
